// Related links with images
.related-links--with-images__wrapper {
    padding: $measure--basic;

    &.background--gray2,
    &.background--purewhite {
        span:before {
            color: $color-inline-link;
            font-size: $font-size--alpha;
        }

        a {
            @include anchor-inline($color-inline-link, $color-inline-link);
        }
    }

    &.background--purewhite {
        background-color: $color-purewhite;
    }

    &.background--pink80 {
        background-color: $color-pink80;
    }

    &.background--red60 {
        background-color: $color-red60;
    }

    &.background--pink80,
    &.background--red60 {
        h2 {
            color: $color-purewhite;
        }

        span:before {
            color: $color-gray4;
            font-size: $font-size--alpha;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }
    }

    .related-links--with-images__2-columns {
        display: flex;
        flex-wrap: wrap;

        .related-links--with-images:nth-child(n + 3) {
            margin-top: $measure--basic;
        }
    }

    .related-links--with-images {
        padding: $measure--half;
        flex-basis: 50%;

        @include media('<tablet') {
            flex-basis: 100%;

            &:nth-child(n + 2) {
                margin-top: $measure--basic;
            }
        }

        h2 {
            text-align: left;
            @include betatype();
        }

        img {
            width: 100%;
        }

        a {
            margin-left: $measure--half;
            font-weight: $font-weight-normal;

            &:after {
                display: none;
                content: none;
            }
        }

        .related-links--with-images__links-block {
            display: block;
            margin-top: 0;

            p {
                span {
                    display: inline;
                }

                a {
                    display: inline;
                }
            }
        }
    }
}

// NEW
$colors: (
    blue70: (
        $color-purewhite,
    ),
    gray2: (
        $color-purewhite,
    ),
    pink90: (
        $color-purewhite,
    ),
    red70: (
        $color-purewhite,
    ),
    purewhite: (
        $color-purewhite,
    ),
);

@each $class, $color in $colors {
    $textColor: nth($color, 1);
    .related-links--with-images__cards {
        .card--related-links--with-images {
            &.background--purewhite,
            &.background--gray2 {
                a {
                    @include anchor-inline(
                        $color-inline-link,
                        $color-inline-link,
                        $color-text--dark,
                        $color-outline
                    );
                }
            }
            &:not(.background--purewhite):not(.background--gray2) {
                &.background--#{$class} {
                    color: $textColor;
                    a {
                        &:link,
                        &:visited {
                            color: $textColor;
                            border-bottom-color: $textColor;
                            background-image: none;
                        }
                        &:active,
                        &:focus,
                        &:hover {
                            color: $textColor;
                            background-color: transparent;
                            background-image: none;
                            border-bottom-color: $textColor;
                            outline-color: $textColor;
                        }
                    }
                }
            }
        }
    }
}

.related-links--with-images__cards {
    @include media('>=tablet') {
        display: grid;
        grid-column: 2/-2;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: $measure--small2;
    }

    list-style: none;
    margin: 0;
    max-width: unset;
    padding-bottom: $measure--small1;
    padding-left: $measure--half;
    padding-right: $measure--half;

    .card--related-links--with-images {
        height: max-content;
        .card--related-links--with-images__links__content {
            padding: 0 $measure--basic $measure--basic $measure--basic;
            .card--related-links--with-images__links {
                p {
                    display: table;
                    span {
                        display: table-cell;
                        padding-right: $measure--half;
                    }
                }
            }
        }
    }
}
